import React from "react";
import {Switch} from "react-router-dom";
import EducationalGroup from './container/EducationalGroup/educationalGroup';
import UserAccount from './container/UserAccount/userAccount'
import UserRegistration from './container/UserRegistration/userRegistration'
import AppliedRoute from "./component/AppliedRoute";
export default ({childProps}) =>
    <Switch>
        <AppliedRoute path="/" exact component={EducationalGroup} props={childProps}/>
        <AppliedRoute path="/userAccount" component={UserAccount} props={childProps}/>
        <AppliedRoute path="/userRegistration" component={UserRegistration} props={childProps}/>
    </Switch>
